import { headers } from 'next/headers';

type JsonPrimitive = string | number | boolean | null;
type JsonValue = JsonPrimitive | JsonValue[] | { [key: string]: JsonValue };

interface DashboardData {
  analytics: JsonValue;
  notifications: JsonValue;
  settings: JsonValue;
}

const isRecord = (value: unknown): value is Record<string, unknown> =>
  typeof value === 'object' && value !== null && !Array.isArray(value);

const formatValue = (value: unknown) => {
  if (value === null || value === undefined) {
    return '';
  }

  if (typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean') {
    return String(value);
  }

  return JSON.stringify(value);
};

const resolveBaseUrl = () => {
  const host = headers().get('host');

  if (host) {
    const protocol = process.env.VERCEL ? 'https' : 'http';
    return `${protocol}://${host}`;
  }

  if (process.env.NEXT_PUBLIC_SITE_URL) {
    return process.env.NEXT_PUBLIC_SITE_URL.replace(/\/$/, '');
  }

  if (process.env.NEXT_PUBLIC_VERCEL_URL) {
    return `https://${process.env.NEXT_PUBLIC_VERCEL_URL}`;
  }

  return 'http://localhost:3000';
};

const getDashboardData = async (baseUrl: string): Promise<DashboardData> => {
  const [analyticsRes, notificationsRes, settingsRes] = await Promise.all([
    fetch(`${baseUrl}/api/analytics`, { cache: 'no-store' }),
    fetch(`${baseUrl}/api/notifications`, { cache: 'no-store' }),
    fetch(`${baseUrl}/api/settings`, { cache: 'no-store' })
  ]);

  if (!analyticsRes.ok || !notificationsRes.ok || !settingsRes.ok) {
    throw new Error('Failed to load dashboard data');
  }

  const [analytics, notifications, settings] = await Promise.all([
    analyticsRes.json(),
    notificationsRes.json(),
    settingsRes.json()
  ]);

  return { analytics, notifications, settings };
};

export default async function Dashboard() {
  const baseUrl = resolveBaseUrl();
  const { analytics, notifications, settings } = await getDashboardData(baseUrl);

  const analyticsRecord = isRecord(analytics) ? analytics : {};
  const settingsRecord = isRecord(settings) ? settings : {};
  const notificationList = Array.isArray(notifications) ? notifications : [];

  return (
    <section>
      <h2>Dashboard Content</h2>

      <section>
        <h3>Analytics</h3>
        {Object.keys(analyticsRecord).length ? (
          <dl>
            {Object.entries(analyticsRecord).map(([key, value]) => (
              <div key={key}>
                <dt>{key}</dt>
                <dd>{formatValue(value)}</dd>
              </div>
            ))}
          </dl>
        ) : (
          <pre>{JSON.stringify(analytics ?? {}, null, 2)}</pre>
        )}
      </section>

      <section>
        <h3>Notifications ({notificationList.length})</h3>
        {notificationList.length ? (
          <ul>
            {notificationList.map((notification, index) => {
              const item = isRecord(notification) ? notification : null;
              const key = item?.id ? String(item.id) : String(index);
              const message = item?.message ?? formatValue(notification);
              return <li key={key}>{message}</li>;
            })}
          </ul>
        ) : (
          <p>No notifications available.</p>
        )}
      </section>

      <section>
        <h3>Settings</h3>
        {Object.keys(settingsRecord).length ? (
          <dl>
            {Object.entries(settingsRecord).map(([key, value]) => (
              <div key={key}>
                <dt>{key}</dt>
                <dd>{formatValue(value)}</dd>
              </div>
            ))}
          </dl>
        ) : (
          <pre>{JSON.stringify(settings ?? {}, null, 2)}</pre>
        )}
      </section>
    </section>
  );
}
